@keyframes shapeAFadeInLeft {
    from {
        opacity: 0;
        top: -25%;
        left: -15%;
        transform: rotate(50deg);
    }
    to {
        opacity: 1;
        top: -100px;
        left: -100px;
        transform: rotate(20deg);
    }
}

.shapeAAnim {
    animation-name: shapeAFadeInLeft;
}
