@keyframes avatarWrapperInBottom {
    from {
        transform: translate(0, 100%);
    }
    50% {
        transform: translate(0, -10%);
    }
    to {
        transform: translate(0, 0);
    }
}

.avatarWrapperAnim {
    animation-name: avatarWrapperInBottom;
}

@media screen and (max-width: 959px) {
    @keyframes avatarInBottom {
        from {
            box-shadow: 0 0;
            opacity: 0;
            transform: rotate(50deg);
        }
        to {
            opacity: 1;
            transform: rotate(0deg);
            boxShadow: '0 10px 10px -8px rgba(0,0,0,.78)',
        }
    }
}

@media screen and (min-width: 959px) {
    @keyframes avatarInBottom {
        from {
            box-shadow: 0 0;
            opacity: 0;
            transform: rotate(50deg);
        }
        to {
            opacity: 1;
            transform: rotate(0deg);
            boxShadow: '0 10px 10px -8px rgba(0,0,0,.78)',
        }
    }
}

.avatarAnim {
    animation-name: avatarInBottom;
}

@media screen and (max-width: 959px) {
    @keyframes headerInfoAvatarPadding {
        50% {
            padding: 30px;
        }
        to {
            padding: 15px 20px;
        }
    }
}

.headerInfoAvatarAnim {
    animation-name: headerInfoAvatarPadding;
}

@keyframes secAbtInfoFadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.secAbtInfoFadeInUpAnim {
    animation-name: secAbtInfoFadeInUp;
}

@keyframes secAbtDetailFadeInLeft {
    from {
        opacity: 0;
        transform: translateX(30px);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

.secAbtDetailFadeInLeftAnim {
    animation-name: secAbtDetailFadeInLeft;
}
